.categories{
    width: 100%;
    overflow: scroll;
}

.categoriesNav{
    margin: 0 2vw;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}

.allCategoriesBtn {
    text-decoration: none;
    color: #454545;
    font-size: 14px;
}

.categoriesBlock{
    width: 700px;
    background-color: rgba(255, 255, 255, 0);
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    padding: 10px 10px;
    margin: 0 2vw;
    border-radius: 8px;
    background-color: #FFFFFF;
    overflow: scroll;
}

.categoryItem{
    display: flex;
    flex-direction: column;
    align-content: center;
    align-items: center;
    width: 120px;
    margin: 10px 10px 0 10px;
    text-decoration: none;
    color: black;
}
.categories::-webkit-scrollbar {
    width: 0;
}

.categoryImg{
    width: 60px;
}

.categoryTitle{
    text-align: center;
}
